.header-color {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#7de076+2,34ba72+29,dd6a72+66 */
  background: rgb(125, 224, 118); /* Old browsers */
  background: -moz-linear-gradient(
    left,
    rgba(125, 224, 118, 1) 2%,
    rgba(52, 186, 114, 1) 29%,
    rgba(221, 106, 114, 1) 66%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    rgba(125, 224, 118, 1) 2%,
    rgba(52, 186, 114, 1) 29%,
    rgba(221, 106, 114, 1) 66%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    rgba(125, 224, 118, 1) 2%,
    rgba(52, 186, 114, 1) 29%,
    rgba(221, 106, 114, 1) 66%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7de076', endColorstr='#dd6a72',GradientType=1 ); /* IE6-9 */
}

@media screen and (max-width: 1024px) {
  .mdl-layout__drawer-button {
    color: gray !important;
  }
}

.landing-grid {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#7de076+2,34ba72+29,dd6a72+66 */
  /* background: rgb(125,224,118); */ /* Old browsers */
  /* background: -moz-linear-gradient(left,  rgba(125,224,118,1) 2%, rgba(52,186,114,1) 29%, rgba(221,106,114,1) 66%); */ /* FF3.6-15 */
  /* background: -webkit-linear-gradient(left,  rgba(125,224,118,1) 2%,rgba(52,186,114,1) 29%,rgba(221,106,114,1) 66%); */ /* Chrome10-25,Safari5.1-6 */
  /* background: linear-gradient(to right,  rgba(125,224,118,1) 2%,rgba(52,186,114,1) 29%,rgba(221,106,114,1) 66%); */ /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  /* filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7de076', endColorstr='#dd6a72',GradientType=1 ); */ /* IE6-9 */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-around;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid p {
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

.resume-right-col p {
  text-align: justify;
}
